import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';

import '../../styles/AddCard.css';
import { useState, useRef, createRef, useEffect } from 'react';
import { fetchAddCard } from '../../utils/fetchAddCard';
import { dashOnEmptyInput } from '../../utils/dashOnEmptyInput';

function NorwegianAddVerb({ wordSetter, showModal }) {

    const grammarInputNames = ["Infinitive", "Present", "Past", "Future", "Conditional",
        "Imperative", "Present Perfect", "Past Perfect", "Future Perfect", "Conditional Perfect"];

    const grammarInputLen = grammarInputNames.length;

    const [validated, setValidated] = useState(false);

    const wordInput = useRef(null);
    const pronInput = useRef(null);
    const meanInput = useRef(null);

    const [elRefs, setElRefs] = useState([]);

    useEffect(() => {
        setElRefs(elRefs => (
            Array(grammarInputLen).fill().map((_, i) => elRefs[i] || createRef())
        ));
    }, [grammarInputLen]);

    const addCard = (event) => {
        event.preventDefault();
        event.stopPropagation();

        if (event.currentTarget.checkValidity() === true) {
            setValidated(false);
            wordSetter(wordInput.current.value);
            const cardObj = {
                "word": wordInput.current.value,
                "translation": [
                    {
                        "type": "verb",
                        "pronounciation": pronInput.current.value,
                        "meaning": meanInput.current.value,
                        "grammarVerb": {
                            "infinitive": dashOnEmptyInput(elRefs[0]),
                            "present": dashOnEmptyInput(elRefs[1]),
                            "past": dashOnEmptyInput(elRefs[2]),
                            "future": dashOnEmptyInput(elRefs[3]),
                            "conditional": dashOnEmptyInput(elRefs[4]),
                            "imperative": dashOnEmptyInput(elRefs[5]),
                            "presentPerfect": dashOnEmptyInput(elRefs[6]),
                            "pastPerfect": dashOnEmptyInput(elRefs[7]),
                            "futurePerfect": dashOnEmptyInput(elRefs[8]),
                            "conditionalPerfect": dashOnEmptyInput(elRefs[9])
                        }
                    }
                ]
            }
            fetchAddCard("Norwegian", cardObj).then(data => {
                if (data === "DB_ERR" || data === "MISSING_LANG_PASSED" || data === "MISSING_WORD_PASSED") {
                    console.error(data);
                } else if (data === "Internal Server Error") {
                    console.error("There was a problem with saving your card to the database");
                } else if (data.startsWith("Proxy error:")) {
                    console.error(data);
                } else {
                    clearForm();
                    showModal();
                }
            }).catch((error) => {
                console.error("Error: ", error);
            })
        } else {
            setValidated(true);
        }
    };

    const clearForm = () => {
        for (let i = 0; i < grammarInputLen; i++) {
            elRefs[i].current.value = '';
        }
    }

    return (
        <Form onSubmit={addCard} noValidate validated={validated} data-testid="testNorwegianAddVerbForm">
            <Form.Group as={Row} className="mb-2">
                <Form.Label column sm="2">
                    Word
                </Form.Label>
                <Col sm={10}>
                    <Form.Control ref={wordInput} required type="text" placeholder="Type here" data-testid="testNorAddVerbWord" />
                </Col>
            </Form.Group>
            <Form.Group as={Row} className="mb-2">
                <Form.Label column sm="2">
                    Pronounciation
                </Form.Label>
                <Col sm={10}>
                    <Form.Control ref={pronInput} required type="text" placeholder="Type here" data-testid="testNorAddVerbPron" />
                </Col>
            </Form.Group>
            <Form.Group as={Row} className="mb-2">
                <Form.Label column sm="2">
                    Meaning
                </Form.Label>
                <Col sm={10}>
                    <Form.Control ref={meanInput} required type="text" placeholder="Type here" data-testid="testNorAddVerbMean" />
                </Col>
            </Form.Group>
            {
                grammarInputNames.map((name, index) =>
                    <Form.Group key={name} as={Row} className="mb-2">
                        <Form.Label column sm="2">
                            {name}
                        </Form.Label>
                        <Col sm={10}>
                            <Form.Control ref={elRefs[index]} className="dontValidate" type="text" placeholder="Type here" />
                        </Col>
                    </Form.Group>)
            }
            <Button variant="success" type="submit" data-testid="testNorAddVerbButton">
                Add Card
            </Button>
        </Form>
    );
}

export default NorwegianAddVerb;